#Search {
  display: flex;
  flex-direction: column;
  align-items: center;
  form {
    display: flex;
    align-items: center;
    height: 100%;
    & > * {
      flex-grow: 1;
      margin: 10px;

      height: 40px;
      &:not(button) {
        padding: 10px;
      }
    }
  }
}
